          -webkit-animation: button-ani 10s infinite;
                  animation: button-ani 10s infinite; }
  body #root main .container {
    min-height: 100vh;
    max-width: 1366px; }
    body #root main .container .projects {
      border: 1px solid; }
      body #root main .container .projects h1 {
        text-align: center;
        font-size: 2em;
        margin: 50px auto;
        color: #3d7c47; }
      body #root main .container .projects ul {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(250px, 0.5fr));
        grid-gap: 10px;
        gap: 10px;
        width: 75%;
        margin: auto; }
        body #root main .container .projects ul li {
          display: grid;
          grid-auto-rows: 5fr 2fr;
          text-align: center;
          overflow: hidden;
          box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3), 0 0 40px rgba(0, 0, 0, 0.1) inset; }
          body #root main .container .projects ul li:hover img {
            -webkit-transform: width 2s;
                    transform: width 2s; }
          body #root main .container .projects ul li:hover span {
            display: block; }
          body #root main .container .projects ul li span:nth-child(1) img {
            -webkit-transform: scale(0.95);
                    transform: scale(0.95);
            width: 100%; }
          body #root main .container .projects ul li span:nth-child(2) {
