      width: 100%;
      display: grid;
      place-items: center;
      position: relative;
      overflow: hidden; }
      body #root main .about-me aside h1 {
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
        position: absolute;
        background-color: #252525;
        color: transparent;
        text-shadow: 1px 1px 1px rgba(255, 255, 255, 0.5);
        -webkit-background-clip: text;
        -moz-background-clip: text;
        background-clip: text; }
  body #root main #contact, body #root main #resume, body #root main #footer {
    min-height: 100vh;
    font-size: 1em; }

@-webkit-keyframes button-ani {
  25% {
    border-color: red;
    color: red; }
  50% {
    border-color: lime;
    color: lime; }
  75% {
    border-color: blue;
    color: blue; } }

@keyframes button-ani {
  25% {
    border-color: red;
    color: red; }
  50% {
    border-color: lime;
    color: lime; }
  75% {
    border-color: blue;
    color: blue; } }

@media only screen and (max-width: 800px) {
  body #root main .landing-page-container .background-info-container .background-info {
    font-size: .65em; }
  body #root main .about-me div {
    padding: 50px; }
  body #root main .about-me aside {
    font-size: 2em; } }

